import React ,{Component} from 'react';
import {Form, Input, DatePicker,Select  }  from 'antd'
import axios  from 'axios'
const { TextArea } = Input;
const { Option}= Select
const  loginid=localStorage.getItem('loginid');
const user=localStorage.getItem('name');
class cusTiaozheng  extends Component{
      constructor(props){
        super(props)
        this.state={
             area:[],
             ye:[],
             cheng:[],
             qu:[],
             zu:[],
             peo:[],

        }
      }
      componentDidMount(){
        //  this.getArea(); 
         this.getCity();

      }
      getCity(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/qu/one',{ 
          name:user,
          num:loginid
           })
           .then((res)=>{
             if(res.data.code==200){
              console.log(res.data.data);
              var city=res.data.data;
              if(city){
                  this.setState({
                    cheng:city
                  })
              }
             
             }else {
             
             }
           }).catch(error=>{
                console.log(error)
           })
          }
      }
      handleSelectCity(value){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/qu/two',{ 
          name:user,
          num:loginid,
          id:value
           })
           .then((res)=>{
             if(res.data.code==200){
              console.log(res.data.data);
              var num=res.data.data;
              if(num){
                  this.setState({
                    qu:num
                  })
              }
             
             }else {
             
             }
           }).catch(error=>{
             console.log(error)
           })
          }
    
      }
      handleSelectQu(value){
        console.log(value);
        if(loginid!='' && user!=""){
       var loginid=localStorage.getItem('loginid');
       var user=localStorage.getItem('name');
     axios.post('/index.php/qu/two',{ 
       name:user,
       num:loginid,
       id:value
        })
        .then((res)=>{
          if(res.data.code==200){
           console.log(res.data.data);
           var data=res.data.data;
           if(data){
               this.setState({
                 zu:data
               })
           }
          
          }else {
          
          }
        }).catch(error=>{
          console.log(error)
        })
       }
    }
    changeZu=(value)=>{
      console.log(value);
      if(loginid!='' && user!=""){
       var loginid=localStorage.getItem('loginid');
       var user=localStorage.getItem('name');
     axios.post('/index.php/qu/peo',{ 
       name:user,
       num:loginid,
       id:value
        })
        .then((res)=>{
          if(res.data.code==200){
           console.log(res.data.data);
           var number=res.data.data;
           if(number){
               this.setState({
                 peo:number
               })
           }
          
          }else {
          
          }
        }).catch(error=>{
             console.log(error)
        })
       }
 }

      getArea=()=>{
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
         var user=localStorage.getItem('name');
          axios.post('/index.php/user/change',{
             num:loginid,
             name:user,
         })
         .then((res)=>{
           console.log(res);
           if(res.data.code==200){
            console.log('区域');
             var num=res.data.data;
             this.setState({
              area:num
             })
           }
         }).catch((error=>{
            console.log(error);
         }))
      }
    }
   
    selectChangeCustomer=(value)=>{
         console.log(value);
          if(loginid!='' && user!=""){
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            axios.post('/index.php/user/get',{
              num:loginid,
              name:user,
              id:value
          })
          .then((res)=>{
           
            if(res.data.code==200){
              console.log(res);
              console.log('业务员');
              var num=res.data.data;
              if(num.length>0){
                this.setState({
                  ye:num
                })
              }
             
            }
          }).catch((error=>{
              console.log(error);
          }))
        }
    
    }

      render(){
        const { area,ye, cheng,qu,zu,peo} =this.state;
        const { getFieldDecorator } = this.props.form;
        const formItemLayout={
            labelCol:{
              span:4
            },
            wrapperCol:{
               span:20
              }
        }
        return(
          <div className="news">
               <Form > 
               {/* <Form.Item label="转入区域" {...formItemLayout}>
          {getFieldDecorator('time', {
            rules: [{ required: false, message: '请选择跟进时间!' }],
          })(
            <Select  onChange={this.selectChangeCustomer} >
              {
                  area.length>0?area.map((p,q)=>{
                       return(
                        <Option key={q} value={p.id}>{p.name}</Option>
                       )
                  }):''
              }
            </Select>
           
          )}
        </Form.Item> */}
         <Form.Item label="城市" layout="inline" {...formItemLayout}>
            {getFieldDecorator('city', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                    <Select   onChange={this.handleSelectCity.bind(this)} allowClear>
                    {
                      cheng.length>0?cheng.map((m,n)=>{
                            return(
                              <Option value={m.id} key={m.id}>{m.name}</Option>
                            )
                      }) : ''
                  }
                </Select>
              )}
            </Form.Item>
            <Form.Item label="项目区域" layout="inline"  {...formItemLayout}>
            {getFieldDecorator('area', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select  onSelect={this.handleSelectQu.bind(this)} allowClear>
                {
                  qu.length>0?qu.map((m,n)=>{
                        return(
                         <Option value={m.id} key={n}>{m.name}</Option>
                        )
                  }) :''
             } 
               </Select>
              )}
            </Form.Item>
            <Form.Item label="组别" layout="inline" allowClear {...formItemLayout}>
            {getFieldDecorator('department', {
                rules: [{ required: false, message: '请选择省' }],
              })(
                <Select   onChange={this.changeZu.bind(this)} allowClear>
                {
                     zu.length>0 ? zu.map((m,n)=>{
                          return(
                            <Option value={m.id } key={n}>{m.name} </Option>
                          )
                     }) :''
                }
             
  
              </Select>
              )}
            </Form.Item>
            <Form.Item label="人员"  {...formItemLayout}>
                {getFieldDecorator('id', {
                    rules: [{ required: false, message: '请选择省' }],
                  })(
                    <Select
                      placeholder="所有"
                      onChange={this.handleSelectChange}
                      allowClear
                    >
                      {
                          peo.length>0 ? peo.map((m,n)=>{
                            return(
                              <Option value={m.id } key={n}>{m.name} </Option>
                            )
                       }) :''
                      }
                    </Select> 
                  )}
              </Form.Item>



        {/* <Form.Item  label="业务员" {...formItemLayout}>
          {getFieldDecorator('t_time', {
            rules: [{ required: false, message: '请选择提醒日期!' }],
          })(
            <Select>
               {
                  ye.length>0?ye.map((p,q)=>{
                       return(
                        <Option key={q} value={p.id}>{p.name}</Option>
                       )
                  }):''
              }
            </Select>
           
          )}
        </Form.Item> */}
               </Form>
          </div>
          )
      }
}
const cusTiaozhengs=Form.create()(cusTiaozheng)
  export default cusTiaozhengs