import React ,{Component} from 'react';
import {
    Form,
    Input,
    Button,
    Breadcrumb,
    Row,
    message,
    Modal
} from 'antd';
import axios from 'axios'
import $ from 'jquery'
class AddPingForm extends Component{
      constructor(props){
        super(props)
        this.state={
             detail:[],
        }
      }
  componentDidMount(){
    var key=sessionStorage.getItem('key')
    console.log(key);
    this.getDetail();
  }


  getDetail(){
    var key=sessionStorage.getItem('key');
    var loginid=localStorage.getItem('loginid');
    var user=localStorage.getItem('name');
    if(loginid!=='' && user!==""){
      axios.post('/index.php/zhi/comments',{
          bid:key,
          name:user,
          num:loginid
      })
      .then((res)=>{
            if(res.data.code==200){
              console.log(res.data.data);
              var num=res.data.data;
              if(num.length>0){
                this.setState({
                  detail:res.data.data,
                  // gid:res.data.data.gid
               })
              }
              //  console.log(this.state.gid);
            
             
            }
      }).catch((error)=>{
        console.log(error)
      })
    }


   }



delete=(num)=>{
  var loginid=localStorage.getItem('loginid');
  var user=localStorage.getItem('name');
      console.log(num);
      var id=num;
      if(loginid!=='' && user!==""){
        axios.post('/index.php/zhi/delete/comment',{
            id:id,
            name:user,
            num:loginid
        })
        .then((res)=>{
              if(res.data.code==200){
                console.log(res)
                Modal.success({
                  title: '删除评论成功',
                  content: '您已经成功删除评论',
                });
                 window.location.reload();
              }else{
                Modal.error({
                  title: '删除评论失败',
                  content: '删除评论失败，请重新删除',
                });
              }
        }).catch((error)=>{
          console.log(error)
        })
      }
 
}
update=(e)=>{
  var loginid=localStorage.getItem('loginid');
  var user=localStorage.getItem('name');
  var key=sessionStorage.getItem('key'); //项目id
  var id=e.target.getAttribute('data-id');
  console.log(id);
  var dom=e.target.parentNode.children[2];
  var  val=dom.firstElementChild.firstElementChild.firstElementChild.firstElementChild.value
  console.log(val);
  if(loginid!=='' && user!==""){
    axios.post('/index.php/zhi/ping',{
        id:id,
        bid:key,
        name:user,
        num:loginid,
        content:val,
    })
    .then((res)=>{
          if(res.data.code==200){
            // console.log(res)
          
           Modal.success({
            title: '更新评论成功',
            content: '您已经成功更新评论',
            });
            window.location.reload();
          }else{
            Modal.error({
              title: '更新评论失败',
              content: '更新评论失败，请重新提交',
              });
          }
    }).catch((error)=>{
      console.log(error)
    })
  }


}

      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const { detail } =this.state;
        return(
          <div className="ping">
              <div className="bread">
                <Breadcrumb>
                    <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
                    <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
                    <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
                    <Breadcrumb.Item href="">编辑评论 </Breadcrumb.Item>
                </Breadcrumb>
            </div>
            <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        {
           detail.length>0 ? detail.map((m,n)=>{
               return(
                <Row>
                    <span>项目名称:   {m.building_name}</span>
                    <p className="title2">评价内容：</p>
                    <Form.Item >
                       <TextArea rows={10}   autosize={{ minRows: 10, maxRows: 50 }}  style={{width:1000}} placeholder="请至少输入10个字" defaultValue={m.content}/>,
                    </Form.Item >
                    <Button type="primary" onClick={this.delete.bind(this,m.id)} style={{marginRight:'20px'}}>
                           删除
                      </Button>
                     <Button type="primary"    onClick={this.update.bind(this)} style={{marginTop:'20px'}} data-id={m.id} data-gid={m.gid}>
                         更新
                    </Button>
               </Row>
               )
           }) :''
        }
     
        
      
        <br/>
          </Form>




          </div>
          )
      }
}
const AddPing=Form.create()(AddPingForm);
  export default AddPing