import React ,{Component} from 'react';
import { Form, 
    Input, 
    Button, 
    Breadcrumb ,
    Col,
    Row, 
    Select
} from 'antd'
import axios from 'axios'
const {Option}=Select
class dongZeForm extends Component{
      constructor(props){
        super(props)
        this.state={
            cheng:[],
            qu:[],
            zu:[],
            peo:[],
        }
      }
    componentDidMount(){
        this.getCity();
    }
    getCity(){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/qu/one',{ 
          name:user,
          num:loginid
           })
           .then((res)=>{
             if(res.data.code==200){
              console.log(res.data.data);
              var city=res.data.data;
              if(city){
                  this.setState({
                    cheng:city
                  })
              }
             
             }else {
             
             }
           }).catch(error=>{
                console.log(error)
           })
          }
      }
      handleSelectCity(value){
        if(loginid!='' && user!=""){
          var loginid=localStorage.getItem('loginid');
          var user=localStorage.getItem('name');
        axios.post('/index.php/qu/two',{ 
          name:user,
          num:loginid,
          id:value
           })
           .then((res)=>{
             if(res.data.code==200){
              console.log(res.data.data);
              var num=res.data.data;
              if(num){
                  this.setState({
                    qu:num
                  })
              }
             
             }else {
             
             }
           }).catch(error=>{
             console.log(error)
           })
          }
    
      }
      handleSelectQu(value){
        console.log(value);
        if(loginid!='' && user!=""){
       var loginid=localStorage.getItem('loginid');
       var user=localStorage.getItem('name');
     axios.post('/index.php/qu/two',{ 
       name:user,
       num:loginid,
       id:value
        })
        .then((res)=>{
          if(res.data.code==200){
           console.log(res.data.data);
           var data=res.data.data;
           if(data){
               this.setState({
                 zu:data
               })
           }
          
          }else {
          
          }
        }).catch(error=>{
          console.log(error)
        })
       }
    }
    changeZu=(value)=>{
        console.log(value);
        if(loginid!='' && user!=""){
         var loginid=localStorage.getItem('loginid');
         var user=localStorage.getItem('name');
       axios.post('/index.php/qu/peo',{ 
         name:user,
         num:loginid,
         id:value
          })
          .then((res)=>{
            if(res.data.code==200){
             console.log(res.data.data);
             var number=res.data.data;
             if(number){
                 this.setState({
                   peo:number
                 })
             }
            
            }else {
            
            }
          }).catch(error=>{
               console.log(error)
          })
         }
   }
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key=sessionStorage.getItem('key')
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(loginid,user);
            if(loginid!='' && user!=""){
            console.log(key);
            axios.post('/index.php/zhi/fuze',{
              bid:key,
              id:values.id,
              name:user,
              num:loginid
         }).then((res)=>{
                if(res.data.code==200){
                 console.log('提交团购成功')
               }
         }).catch((error)=>{
               console.log(error)
         })
              }

          }
        });
      };



      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        const {  cheng,qu,zu,peo  }=this.state;
        return(
          <div className="dongze">
               <div className="bread">
                <Breadcrumb>
                    <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
                    <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
                    <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
                    <Breadcrumb.Item href="">添加负责人 </Breadcrumb.Item>
                </Breadcrumb>
            </div>
            <Form layout="inline" onSubmit={this.handleSubmit}>
                <br/>
                <Form.Item label="城市" layout="inline">
                {/* {getFieldDecorator('city', {
                    rules: [{ required: false, message: '请选择省' }],
                })( */}
                        <Select  style={{ width:1000}} onChange={this.handleSelectCity.bind(this)} allowClear>
                        {
                        cheng.length>0?cheng.map((m,n)=>{
                                return(
                                <Option value={m.id} key={m.id}>{m.name}</Option>
                                )
                        }) : ''
                    }
                    </Select>
                {/* )} */}
                </Form.Item>
                <br/>
                <Form.Item label="项目区域" layout="inline" >
            {/* {getFieldDecorator('area', {
                rules: [{ required: false, message: '请选择省' }],
              })( */}
                <Select style={{ width:1000,marginRight:20}} onSelect={this.handleSelectQu.bind(this)} allowClear>
                {
                  qu.length>0?qu.map((m,n)=>{
                        return(
                         <Option value={m.id} key={n}>{m.name}</Option>
                        )
                  }) :''
             } 
               </Select>
              {/* )} */}
            </Form.Item>
            <br/>
            <Form.Item label="组别" layout="inline" allowClear>
            {/* {getFieldDecorator('department', {
                rules: [{ required: false, message: '请选择省' }],
              })( */}
                <Select  style={{ width:1000,}} onChange={this.changeZu.bind(this)} allowClear>
                {
                     zu.length>0 ? zu.map((m,n)=>{
                          return(
                            <Option value={m.id } key={n}>{m.name} </Option>
                          )
                     }) :''
                }
             
  
              </Select>
              {/* )} */}
            </Form.Item>
            <br/>
            <Form.Item label="人员"  >
                {getFieldDecorator('id', {
                    rules: [{ required: false, message: '请选择省' }],
                  })(
                    <Select
                      placeholder="所有"
                      onChange={this.handleSelectChange}
                      style={{width:1000}}
                      allowClear
                    >
                      {
                          peo.length>0 ? peo.map((m,n)=>{
                            return(
                              <Option value={m.id } key={n}>{m.name} </Option>
                            )
                       }) :''
                      }
                    </Select> 
                  )}
              </Form.Item>
              <br/>
                <Form.Item>
                    <Button type="primary" htmlType="submit" className="sub">
                         提交
                    </Button>
                </Form.Item>
                </Form>
          </div>
          )
      }
}
const  dongZe=Form.create()(dongZeForm)
  export default dongZe 