import React ,{Component} from 'react';
import {
    Form,
    Input,
    Button,
    Breadcrumb,
    Row,
    Modal 
} from 'antd';
import axios from 'axios'
class AddPingForm extends Component{
      constructor(props){
        super(props)
      }
  componentDidMount(){
    var key=sessionStorage.getItem('key')
    console.log(key);
  }

  
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log('输入的结果为 ', values);
            var key=sessionStorage.getItem('key')
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(key);
            if(loginid!='' && user!=""){
                console.log(key);
                axios.post('/index.php/zhi/ping',{
                  bid:key,
                  content:values.content,
                  name:user,
                  num:loginid
             }).then((res)=>{
                    if(res.data.code==200){
                     console.log('提交评论成功')
                      Modal.success({
                        title: '提交评论成功',
                        content: '您已经成功提交评论',
                      });
                   }else{
                    Modal.error({
                      title: '提交评论失败',
                      content: '提交评论失败，请重新提交',
                    });
                   }
             }).catch((error)=>{
                   console.log(error)
             })
            }
        }

    })
}


editping=()=>{
   this.props.history.push('/projectmanage/projectInformation/editping')
}


      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        return(
          <div className="ping">
              <div className="bread">
                <Breadcrumb>
                    <Breadcrumb.Item href="/"> 项目管理</Breadcrumb.Item>
                    <Breadcrumb.Item href=""> 项目信息 </Breadcrumb.Item>
                    <Breadcrumb.Item href="">项目列表 </Breadcrumb.Item>
                    <Breadcrumb.Item href="">添加评论 </Breadcrumb.Item>
                </Breadcrumb>
            </div>
            <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2">评价内容：</p>
            <br/>
            <Form.Item >
            {getFieldDecorator('content', {
                })(
            <TextArea rows={10}   autosize={{ minRows: 10, maxRows: 50 }}  style={{width:1000}} placeholder="请至少输入10个字"/>,
                )}
            </Form.Item >
        </Row>
        
      
        <br/>
        <Row>
            <Form.Item>
                <Button type="primary" style={{marginRight:800}}  onClick={this.editping}>
                 历史评论
                </Button>
                <Button type="primary" htmlType="submit" className="sub">
                提交
                </Button>
            </Form.Item>

        </Row>
      
          </Form>




          </div>
          )
      }
}
const AddPing=Form.create()(AddPingForm);
  export default AddPing